import React from 'react'
import PropTypes from 'prop-types'
import { withStyles } from '@material-ui/core/styles'
import Card from '../mui-override/Card'
import CardActionArea from '@material-ui/core/CardActionArea'
import CardActions from '@material-ui/core/CardActions'
import CardContent from '@material-ui/core/CardContent'
import Typography from '@material-ui/core/Typography'
import { Link } from 'gatsby'
import {ColorlessTag, ColorlessCat} from '../utils/hash-colorful-tag'
import LocalOfferIcon from '@material-ui/icons/LocalOffer';
import FolderOpenIcon from '@material-ui/icons/FolderOpen';

const styles = {
    card: {
        maxWidth: 800,
        margin: '1em auto',
    },
    media: {
        objectFit: 'cover',
    },
}


function ImgMediaCard(props) {
    const { classes, title, content, slug, tags,categories, date } = props
    const MyLink = props => <Link to={`/posts/${slug}`} {...props} />
    return (
        <Card className={classes.card}>
            <CardActionArea component={MyLink}>
                <CardContent>
                    <Typography gutterBottom variant="h5" component="h2" style={{color:'black'}}>
                        {title}
                    </Typography>
                    <Typography component="p" className="post-description" style={{color:'#555'}}>
                        {content}
                    </Typography>
                </CardContent>
            </CardActionArea>
            <CardActions>
                <div style={{
                    background: '#eee',
                    color: '#000',
                    display: 'flex',
                    alignItems: 'center',
                    flexShrink: 0,
                    height: '24px',
                    borderRadius: '3px',
                    paddingLeft: '8px',
                    paddingRight: '8px',
                    fontSize: '16px',
                    lineHeight: '120%',
                    fontWeight: '400',
                    margin: '0px 6px 0px 0px',
                }}>{date}</div>
                Category:
                <div className="categories">
                {/* <FolderOpenIcon style={{
                    color: '#0099CC',
                    fontSize: '24px',
                    lineHeight: '100%',
                    height: '24px',
                }}/> */}
                <span>{categories && categories.map(category => <ColorlessCat category={category} key={category} />)}</span></div>
                {/* <LocalOfferIcon style={{
                    color: '#FF6666',
                    fontSize: '24px',
                    lineHeight: '100%',
                    height: '24px',
                }}/> */}
                &nbsp;Tags:
                <span>{tags && tags.map(tag => <ColorlessTag tag={tag} key={tag} />)}</span>
            </CardActions>
        </Card>
    )
}

ImgMediaCard.propTypes = {
    classes: PropTypes.object.isRequired,
}

export default withStyles(styles)(ImgMediaCard)
