//生成文章摘要，参考：https://www.cnblogs.com/zerogo/archive/2008/10/30/2209257.html
export function Generate_Brief(html, length){
    html=html.replace(/<(style|script|iframe)[^>]*?>[\s\S]+?<\/\1\s*>/gi,'').replace(/<[^>]+?>/g,'').replace(/\s+/g,' ').replace(/ /g,' ').replace(/>/g,' ');
    if (html.length < length) return html;
    var Foremost = html.substr(0, length);
    var re = /<(\/\/?)(BR|BODY|SCRIPT|P|DIV|H1|H2|H3|H4|H5|H6|ADDRESS|PRE|TABLE|TR|TD|TH|INPUT|SELECT|TEXTAREA|OBJECT|A|UL|OL|LI|BASE|META|LINK|HR|BR|PARAM|IMG|AREA|INPUT|SPAN)[^>]*(>?)/ig;
    var Singlable = /BASE|META|LINK|HR|BR|PARAM|IMG|AREA|INPUT/i
    var Stack = new Array(), posStack = new Array();
    while (true) {
        var newone = re.exec(Foremost);
        if (newone == null) break;
        if (newone[1] == "") {
            var Elem = newone[2];
            if (Elem.match(Singlable) && newone[3] != "") continue;
            Stack.push(newone[2].toUpperCase());
            posStack.push(newone.index);
            if (newone[3] == "") break;
        } else {
            var StackTop = Stack[Stack.length - 1];
            var End = newone[2].toUpperCase();
            if (StackTop == End) {
                Stack.pop();
                posStack.pop();
                if (newone[3] == "") Foremost = Foremost + ">";
            }
        };
    }
    var cutpos = posStack.shift();
    Foremost = Foremost.substring(0, cutpos);
    return Foremost+"...";
}