import React from 'react'
import PropTypes from 'prop-types'
import { withStyles } from '@material-ui/core/styles'
import withRoot from '../../withRoot'
import Layout from '../layout'
import PostListItem from '../post/post-list-item'
import {ColorlessCat} from '../utils/hash-colorful-tag'
import { graphql } from 'gatsby'
import {Generate_Brief} from '../utils/common-fun'

const styles = theme => ({
    root: {
        textAlign: 'center',
        paddingTop: theme.spacing.unit * 20,
    },
    index: {
        margin: '0 auto',
        maxWidth: 800,
        marginTop: '3em',
    },
})

class Index extends React.Component {
    render() {
        const { classes, data } = this.props
        const { category } = this.props.pageContext
        return (
            <Layout>
                <div className={classes.index}>
                    <div style={{ display: 'flex' }}>
                        <ColorlessCat category={category} />分类下共有{data.allPosts.totalCount}篇文章
                    </div>

                    {data.allPosts.edges.map(({ node }) => (
                        <PostListItem title={node.name}
                            key={node.id}
                            content={(node.description !=null && node.description !=undefined && node.description.trim() !="" ) ? node.description : Generate_Brief(node.html,200)}
                            slug={node.slug}
                            image={node.image}
                            tags={node.tags}
                            categories={node.categories}
                            date={node.public_date}
                        />
                    ))}
                </div>
            </Layout>
        )
    }
}

Index.propTypes = {
    classes: PropTypes.object.isRequired,
}

export default withRoot(withStyles(styles)(Index))

export const query = graphql`
query ($category: String!){
    allPosts(filter:{categories: {in: [$category]},status: {eq: "published"}},sort: { fields: [public_date], order: DESC }) {
        edges{
          node{
            description
            html
            id
            name
            tags
            categories
            public_date
            slug
          }
        }
        totalCount
      }
  }
`