import React from 'react'
import { Link } from 'gatsby'

String.prototype.hashCode = function () {
  var hash = 0, i, chr
  if (this.length === 0) return hash
  for (i = 0; i < this.length; i++) {
    chr = this.charCodeAt(i)
    hash = ((hash << 5) - hash) + chr
    hash |= 0 // Convert to 32bit integer
  }
  return hash
}

const hashColorMap = [
  '#f44336',
  '#e91e63',
  '#9c27b0',
  '#673ab7',
  '#3f51b5',
  '#2196f3',
  '#009688',
  '#ff5722',
]
const hashLessColorMap = [
  '#e91e63',
  '#009688',
  '#673ab7',
  '#9c27b0',
  '#3f51b5',
  '#f44336',
  '#2196f3',
  '#ff5722',
]

const getHashColor = (tagcat) => {
  return hashColorMap[Math.abs(Math.ceil(tagcat.hashCode())) % 7]
}
const getHashLessColor = (tagcat) => {
  return hashLessColorMap[Math.abs(Math.abs(tagcat.hashCode())) % 7]
}




const ColorfulCat = (props) => {
  let color = getHashColor(props.category)
  return <Link to={props.to ? props.to : `/categories/${props.category}`}>
    <div style={{
      background: color,
      color: '#fff',
      display: 'inline-block',
      height: '24px',
      borderRadius: '3px',
      paddingLeft: '8px',
      paddingRight: '8px',
      fontSize: '14px',
      lineHeight: '24px',
      fontWeight: '400',
      margin: '0px 6px 0px 0px',
    }}>{
        props.category
      }</div>
  </Link>
}
const ColorfulTag = (props) => {
  let color = getHashColor(props.tag)
  return <Link to={props.to ? props.to : `/tags/${props.tag}`}>
    <div style={{
      background: color,
      color: '#fff',
      display: 'inline-block',
      height: '24px',
      borderRadius: '3px',
      paddingLeft: '8px',
      paddingRight: '8px',
      fontSize: '14px',
      lineHeight: '24px',
      fontWeight: '400',
      margin: '0px 6px 0px 0px',
    }}>{
        props.tag
      }</div>
  </Link>
}


const ColorlessTag = (props) => {
  return <Link className='post-tag' to={props.to ? props.to : `/tags/${props.tag}`}>
    <div style={{
      color: '#FF6666',
      display: 'inline-block',
      height: '24px',
      borderRadius: '3px',
      fontSize: '16px',
      lineHeight: '24px',
      margin: '0px 3px 0px 0px',
    }}>{props.tag}</div><span className="next-tag">,</span>
  </Link>
}

const ColorlessCat = (props) => {
  return <Link className='post-category' to={props.to ? props.to : `/categories/${props.category}`}>
    <div style={{
      color: '#0099CC',
      display: 'inline-block',
      height: '24px',
      borderRadius: '3px',
      fontSize: '16px',
      lineHeight: '24px',
      fontWeight: '400',
    }}>{props.category}</div><span className="next-cate">&gt;</span>
  </Link>
}

export { getHashColor, getHashLessColor, ColorlessTag, ColorlessCat, ColorfulTag, ColorfulCat }